.Card .Word {
  font-family: BodoniMT;
}

.MainContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.CardContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.CardContainer .Card:hover {
  box-shadow: 0 0 10px rgb(230, 230, 151);
  cursor: pointer;
}
.CardContainer .Arrow:hover {
  border-top: 10px solid rgb(255, 255, 168);
  border-right: 10px solid rgb(255, 255, 168);
  cursor: pointer;
}

.Card {
  width: 400px;
  height: 250px;
  background-color: rgb(255, 255, 168);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.Card .Word {
  margin: auto;
  font-size: 60px;
  color: rgb(43, 43, 43);
}
.Card .Transcription {
  margin-bottom: 20px;
  color: rgb(88, 88, 88);
}

.Arrow {
  width: 60px;
  height: 60px;
  border-top: 10px solid rgb(230, 230, 151);
  border-right: 10px solid rgb(230, 230, 151);
  margin-top: auto;
  margin-bottom: auto;
}

.Left {
  transform: rotate(-135deg);
}

.Right {
  transform: rotate(45deg);
}

.Know {
  margin: auto;
  padding: 30px;
  color: rgb(88, 88, 88);
  font-size: smaller;
}

.Sentence {
  background-color: rgb(255, 252, 214);
  padding: 40px;
  color: rgb(43, 43, 43);
}/*# sourceMappingURL=Main.css.map */