.grid-container {
    display: grid;
    grid-template-rows: 1fr 1fr; 
    // grid-template-columns: 1fr 1fr; 
    justify-content: center; 
    align-items: center;
    text-align: center; 

    
    .g1{
        grid-row: 1 / 2;
        grid-column: 1 / 2;
    }
    .g2{
        grid-row: 1 / 2;
        grid-column: 2 / 3;
    }
    .g3{
        grid-row: 2 / 3;
        grid-column: 1 / 3;
    }


}
